<template lang="html">
    <div class="quote-sidebar">
        <div class="gradient-border" />

        <header class="quote-sidebar__header">
            <div class="row space-between">
                <h2 v-if="isValidFirstStep">
                    Confirm & Go
                </h2>
                <h2 v-if="!isValidFirstStep">
                    Price Quote
                </h2>
                <div
                    class="quote-sidebar__header-alert"
                >
                    <span
                        class="report"
                        @click="openModal('report-modal')"
                    >
                        <AlertCircleOutlineIcon />
                        <span class="tool">
                            Report a problem
                        </span>
                    </span>
                    <span
                        class="clear"
                        @click="modalClearForm"
                    >
                        <DeleteOutline />
                        <span class="tool">
                            Clear form
                        </span>
                    </span>
                </div>
            </div>
        </header>

        <QuoteSidebarInitialMessage
            v-if="!isValidFirstStep"
            :text="getQuoteMessage()"
        />

        <section v-else>
            <div class="quote-sidebar__data">
                <span class="quote-sidebar__data-name">Due date: </span>
                <span class="quote-sidebar__data-value">{{ duedate | moment_short_text }}</span>
            </div>
            <div class="quote-sidebar__data">
                <span class="quote-sidebar__data-name">Turnaround: </span>
                <span class="quote-sidebar__data-value">{{ turnaroundHumanize('hours') }}</span>
            </div>
            <div class="quote-sidebar__data">
                <span class="quote-sidebar__data-name">Project: </span>
                <span class="quote-sidebar__data-value">{{ getProjectType() }}</span>
            </div>
            <div class="quote-sidebar__data">
                <span class="quote-sidebar__data-name">Level: </span>
                <span class="quote-sidebar__data-value">{{ getProjectLevel() }}</span>
            </div>
            <div class="quote-sidebar__quotation">
                <div
                    v-show="pages > 0"
                    class="quote-sidebar__row space-between"
                >
                    <div>
                        <FileOutlineIcon class="quote-sidebar__icon" />
                        {{ pages }} pages x {{ getPagesRate() }}
                    </div>
                    <span class="quote-sidebar__quotation-price">{{ getPagesAmount() | money }}</span>
                </div>
                <div
                    v-show="slides > 0"
                    class="quote-sidebar__row space-between"
                >
                    <div>
                        <ImageOutlineIcon class="quote-sidebar__icon" />
                        {{ slides }} slides x {{ getSlidesRate() }}
                    </div>
                    <span class="quote-sidebar__quotation-price">
                        {{ getSlidesAmount() | money }}
                    </span>
                </div>
                <div
                    v-show="charts > 0"
                    class="quote-sidebar__row space-between"
                >
                    <div>
                        <ChartBoxOutlineIcon class="quote-sidebar__icon" />
                        {{ charts }} charts x {{ getChartsRate() }}
                    </div>
                    <span class="quote-sidebar__quotation-price">
                        {{ getChartsAmount() | money }}
                    </span>
                </div>
                <div
                    v-show="problems > 0"
                    class="quote-sidebar__row space-between"
                >
                    <div>
                        <AlertOutlineIcon class="quote-sidebar__icon" />
                        {{ problems }} problems x {{ getProblemsRate() }}
                    </div>
                    <span class="quote-sidebar__quotation-price">{{ getProblemsAmount() | money }}</span>
                </div>
                <div
                    v-show="preferred_writers.length > 0"
                    class="quote-sidebar__row space-between"
                >
                    <div>
                        Preferred writer (5%)
                    </div>
                    <span class="quote-sidebar__quotation-price">
                        + {{ getPreferredWriterSurchargeAmount() | money }}
                    </span>
                </div>
            </div>

            <div class="quote-sidebar__row space-between">
                <span>Total price:</span>
                <animated-number
                    class="quote-sidebar__total-amount"
                    :value="getTotalNet()"
                    :duration="300"
                    :format-value="formatToPrice"
                />
            </div>

            <div class="quote-sidebar__row">
                <button
                    :class="isSubmitAllowed ? `quote-sidebar__btn active` : `quote-sidebar__btn disable`"
                    :disabled="!isSubmitAllowed"
                    @click.prevent="submit"
                >
                    {{ isEditMode() ? 'Save changes' : 'Add to cart' }}
                </button>
            </div>

            <!-- mobile total -->
            <div :class="mobileTotal ? 'total-mobile active' : 'total-mobile'">
                <span class="total-price">{{ getTotalNet() | money }}</span>
            </div>
            <!-- mobile total -->
        </section>
        <report-modal ref="report-modal" />
    </div>
</template>

<script>
import { eventBus } from '@/helpers/event-bus/index.js'

import filtersMixin from '@/mixins/filtersMixin.js'

// icons
import FileOutline from 'mdi-vue/FileOutline.vue'
import ImageOutline from 'mdi-vue/ImageOutline.vue'
import ChartBoxOutline from 'mdi-vue/ChartBoxOutline.vue'
import AlertOutline from 'mdi-vue/AlertOutline.vue'
import DeleteOutline from 'mdi-vue/DeleteOutline.vue'
// import AccountOutline from 'mdi-vue/AccountOutline.vue'
import AlertCircleOutline from 'mdi-vue/AlertCircleOutline.vue'

import AnimatedNumber from 'animated-number-vue'
import ReportModal from '@/components/modals/ReportModal'

// vuex bindings
import { mapGetters } from 'vuex'
import {
    mapOrderFormFields
} from '@/store/modules/order'

// moment library
import moment from 'moment'

// components
import QuoteSidebarInitialMessage from './QuoteSidebarInitialMessage.vue'

export default {
    name: 'QuoteSidebar',
    components: {
        QuoteSidebarInitialMessage,
        FileOutlineIcon: FileOutline,
        ImageOutlineIcon: ImageOutline,
        ChartBoxOutlineIcon: ChartBoxOutline,
        AlertOutlineIcon: AlertOutline,
        // AccountOutlineIcon: AccountOutline,
        AlertCircleOutlineIcon: AlertCircleOutline,
        AnimatedNumber,
        DeleteOutline,
        ReportModal
    },
    mixins: [
        filtersMixin
    ],
    props: {
        currentStep: {
            type: Number,
            required: true
        }
    },
    data() {
        return {
            totalNet: null,
            quoteMsg: '',
            sources: 1,
            minimumAllowedTimeframe: 3,
            mobileTotal: false
        }
    },
    computed: {
        ...mapGetters('order', [
            'getOrderFormStatus'
        ]),
        ...mapOrderFormFields([
            'form_data.type',
            'form_data.academiclevel',
            'form_data.pages',
            'form_data.duedate',
            'form_data.slides',
            'form_data.charts',
            'form_data.problems',
            'form_data.preferred_writers'
        ]),
        isSubmitAllowed() {
            // allow submit only on last step (3)
            if (this.currentStep !== 3) return false
            return this.getOrderFormStatus
        },
        calculatorData() {
            return this.$store.state.config.pricing;
        },
        isValidFirstStep() {
            return this.type !== '' && this.academiclevel !== '' && this.isTimeframeVerified();
        },
        isAuth() {
            return this.$store.state.client.auth;
        },
        today() {
            return moment().format('YYYY-MM-DD');
        },
        momentFrom() {
            return moment(this.duedate).format('D MM, YYYY H:mm A')
        },
        wordcount() {
            if (this.pages > 0) return this.pages * this.wordsPerPage;
            return 0;
        },
        // wordcountText: function(){
        //     if(this.pages > 0)
        //         return '= ~ ' + this.$options.filters.number(this.pages * this.wordsPerPage) + ' words'
        // },
        pagesNote() {
            let pageLabel;
            this.pages === 1 ? pageLabel = 'page' : pageLabel = 'pages';

            return `${this.pages} double spaced ${pageLabel}  = ~ ${this.formatToNumber(this.wordcount)} words `;
        },
        timeframeVerified() {
            return this.isTimeframeVerified();
        }
    },
    watch: {
        totalNet() {
            clearTimeout(this.timer)
            this.mobileTotal = true
            this.timer = setTimeout(() => {
                this.mobileTotal = false
            }, 7000)
        }
    },
    methods: {
        isEditMode() {
            return (this.$route.name === 'order_edit' && !Number.isNaN(this.$route.params.index))
        },
        isSubmitDisabled() {
            return !this.getOrderFormStatus
        },
        getProjectType() {
            const found = this.calculatorData.type.find((element) => element.value === this.type);
            if (found) {
                return found.text;
            }
            return ''
        },
        getProjectLevel() {
            const found = this.calculatorData.level.find((element) => element.value === this.academiclevel);

            if (found) {
                return found.text;
            }
            return ''
        },
        redirectToLogin() {
            this.$router.push({ name: 'login' });
        },
        getRateHours() {
            const found = this.calculatorData.type.find((element) => element.value === this.type);

            if (found) {
                return found.hours;
            }
            return ''
        },
        getEstimateHours() {
            return this.getRateHours() * this.pages;
        },
        getDueHours() {
            if (this.duedate) {
                const due = moment(`${this.duedate}`)
                const now = moment()
                let hours = moment.duration(due.diff(now))
                hours = hours.asHours()

                if (hours > this.minimumAllowedTimeframe) {
                    return hours
                }

                return false
            }
            return false
        },
        isTimeframeVerified() {
            if (this.getDueHours() > this.getEstimateHours()) {
                return true
            } return false
        },
        getQuoteMessage() {
            if (!this.type && !this.academiclevel && !this.duedate && !this.pages) {
                return 'Start filling out the form to get a price quote';
            }
            if (!this.type) return 'Set the type of project';
            if (!this.academiclevel) return 'Set the level';
            if (!this.pages || this.pages <= 0) return 'Set the number of pages, slides, charts or problems';
            if (!this.duedate) return 'Set the date';
            if (!this.turnaroundHumanize('hours')) {
                return 'Your due date is in the past';
            }
            return `Around ${this.getEstimateHours()} hours is needed to complete your project. Your currently set due date is ${this.turnaroundHumanize('hours')}`;
        },
        getTypeRate() {
            const found = this.calculatorData.type.find((element) => element.value === this.type);
            if (found) {
                return Number(found.rate);
            }
            return 0
        },
        getLevelRate() {
            const found = this.calculatorData.level.find((element) => element.value === this.academiclevel);
            if (found) {
                return Number(found.rate);
            }
            return 0
        },
        getTurnaroundRate() {
            let rate = 0;
            const vm = this;
            this.calculatorData.turnaround.forEach((item) => {
                if (vm.getDueHours() >= Number(item.short_name)) {
                    rate = Number(item.rate);
                }
            });
            return rate;
        },
        getPagesRate() {
            const rateType = this.getTypeRate();
            const rateLevel = this.getLevelRate();
            const rateTurnaround = this.getTurnaroundRate();
            const value = (Number(rateType) + Number(rateLevel) + Number(rateTurnaround));
            return value;
        },
        getSlidesRate() {
            const found = this.calculatorData.extras.find((element) => element.value === 'slide');
            if (found) {
                return Number(found.rate);
            }
            return 0
        },
        getChartsRate() {
            const found = this.calculatorData.extras.find((element) => element.value === 'chart');

            if (found) {
                return Number(found.rate);
            }
            return 0
        },
        getProblemsRate() {
            const found = this.calculatorData.extras.find((element) => element.value === 'problem');

            if (found) {
                return Number(found.rate);
            }
            return 0
        },

        getPagesAmount() {
            const amount = this.getPagesRate() * this.pages;
            return amount;
        },
        getSlidesAmount() {
            const amount = this.getSlidesRate() * this.slides;
            return amount;
        },
        getChartsAmount() {
            const amount = this.getChartsRate() * this.charts;
            return amount;
        },
        getProblemsAmount() {
            const amount = this.getProblemsRate() * this.problems;
            return amount;
        },
        getExtrasAmount() {
            let amount = 0;
            amount += this.getPreferredWriterSurchargeAmount();
            return amount;
        },

        getPreferredWriterSurchargeRate() {
            const rate = 0.05;
            return rate;
        },
        isPreferredWritersSupplied() {
            return (this.preferred_writers.length > 0);
        },
        getPreferredWriterSurchargeAmount() {
            if (this.isPreferredWritersSupplied() === true) {
                const amount = this.getPreferredWriterSurchargeRate() * this.getTotalBeforeExtrasBeforeDiscount();
                return amount;
            }

            return 0;
        },
        isLoyaltyDiscount() {
            return !!this.discount_loyalty;
        },
        getLoyaltyDiscountRate() {
            if (this.auth) {
                const data = this.$store.state.client.client_form.client_data;
                return data.statistics ? Number(data.statistics.loyalty_discount_rate) : null;
            }

            return 0;
        },
        getCurrentDiscountRate() {
            const discountLoyalty = this.getLoyaltyDiscountRate();
            if (this.discountByCode >= discountLoyalty) {
                return Number(this.discountByCode);
            }

            return Number(discountLoyalty);
        },
        getDiscountAmount() {
            const amount = this.getCurrentDiscountRate() * this.getTotalAfterExtrasBeforeDiscount();
            return amount;
        },
        getTotalBeforeExtrasBeforeDiscount() {
            const total = this.getPagesAmount() + this.getSlidesAmount() + this.getChartsAmount() + this.getProblemsAmount();
            return total;
        },
        getTotalAfterExtrasBeforeDiscount() {
            const total = this.getTotalBeforeExtrasBeforeDiscount() + this.getExtrasAmount();
            return total;
        },
        // method that returns order TOTAL
        getTotalNet() {
            const net = this.getTotalAfterExtrasBeforeDiscount() - this.getDiscountAmount()
            this.totalNet = net
            return net;
        },
        formatToPrice(value) {
            return `$ ${value.toFixed(2).replace(/\B(?=(\d{3})+(?!\d))/g, ',')}`;
        },
        turnaroundHumanize(mode) {
            const due = moment(`${this.duedate}`);
            const now = moment();
            if (now > due) return false;
            if (mode === 'hours') {
                return `in ${moment.duration(due.diff(now)).humanize()}`;
            }
            if (mode === 'date') {
                return due.format('dddd');
            }
            return `in ${moment.duration(due.diff(now)).humanize()}|${due.format('dddd, MMMM Do YYYY, h:mm:ss a')}`;
        },
        fixDate() {
            const options = {
                year: 'numeric', month: 'short', day: 'numeric', hour: 'numeric', minute: 'numeric'
            }
            // eslint-disable-next-line no-underscore-dangle
            return new Date(moment()._d).toLocaleDateString('en-US', options)
        },
        // getTitle(){
        //     if(this.title){
        //         return truncate(this.title, {length:'50'})
        //     }
        // },
        // getLabel(array, value){
        //     let found = find(array, {'value': value})
        //     if(found){
        //         return found['text']
        //     }
        // },
        modalClearForm() {
            this.$emit('eventClearForm')
        },
        modalReportProblem() {
            this.$emit('eventReportProblem')
        },
        submit() {
            if (this.getOrderFormStatus === false) {
                eventBus.$emit('showSnackBar', 'There are errors in the form.', 'error')
                return
            }

            let actionParams = {}

            if (this.isEditMode() === true) {
                actionParams = {
                    mode: 'edit',
                    index: this.$route.params.index,
                    total: this.getTotalNet()
                }
            } else {
                actionParams = {
                    mode: 'new',
                    total: this.getTotalNet()
                }
            }

            this.$emit('eventSubmitOrder', actionParams)
        },
        openModal(modalName) {
            return this.$refs[modalName].open(
                {
                    title: 'Clear Form',
                    description: 'Are you sure you want to delete all data from the order form?'
                }
            )
        }

    }
}
</script>
