<template lang="html">
    <main-layout
        :settings-breadcrumbs="settingsBreadcrumbs"
        :hidereport="true"
        :bg-wrapper="true"
        :classes="'order-form wrapper'"
    >
        <template #content>
            <div class="row order-form-wrapper">
                <!-- left column -->
                <div class="order-form__left-column">
                    <price-calculator
                        ref="FormStepOne"
                        :current-step="currentStep"
                        @eventNext="onEventNext"
                    />
                    <assignment-details
                        ref="FormStepTwo"
                        :current-step="currentStep"
                        @eventPrevious="onEventPrevious"
                        @eventNext="onEventNext"
                    />
                    <writer-settings
                        ref="FormStepThree"
                        :current-step="currentStep"
                        @eventPrevious="onEventPrevious"
                        @eventNext="onEventNext"
                    />
                </div>
                <!-- left column-->

                <!-- right column - quote id="sidebar"-->
                <div class="order-form__right-column">
                    <div class="sticky">
                        <quote-sidebar
                            :current-step="currentStep"
                            @eventSubmitOrder="onEventSubmitOrder"
                            @eventClearForm="onEventClearForm"
                            @eventReportProblem="onEventReportProblem"
                        />
                        <div class="safety-block">
                            <span class="safety-header">Safe payments</span>
                            <div class="safety-icon">
                                <img
                                    src="@/assets/img/svg/satefy/site-lock.svg"
                                    alt="sitelock"
                                >
                                <img
                                    src="@/assets/img/svg/satefy/pay-pal-verified.svg"
                                    alt="paypal"
                                >
                                <img
                                    src="@/assets/img/svg/satefy/mastercard.svg"
                                    alt="mastercard"
                                >
                                <img
                                    src="@/assets/img/svg/satefy/visa.svg"
                                    alt="visa"
                                >
                                <img
                                    src="@/assets/img/svg/satefy/mcafee.svg"
                                    alt="mcafee"
                                >
                            </div>
                        </div>
                    </div>
                </div>
                <!-- right column - quote -->
            </div>
        </template>
    </main-layout>
</template>

<script>
// this WORKAROUND is required because:
//  1) we need to load the data from the API and store it in vuex
//  2) component must not be rendered before we have the data in the store
//  3) 'this' is not available, so we must import store
import store from '@/store'

import { eventBus } from '@/helpers/event-bus/'

// components
import MainLayout from '@/layouts/MainLayout';
import PriceCalculator from '@/components/order/PriceCalculator.vue';
import AssignmentDetails from '@/components/order/AssignmentDetails.vue';
import WriterSettings from '@/components/order/WriterSettings.vue';
import QuoteSidebar from '@/components/order/QuoteSidebar.vue'

import { scroller } from 'vue-scrollto/src/scrollTo'

// vuex bindings
import { createNamespacedHelpers } from 'vuex'
import {
    CLEAR_FORM
} from '@/store/modules/order/mutation-types'
import {
    ACTION_EDIT_ORDER,
    ACTION_NEW_ORDER
} from '@/store/modules/order/action-types'

const {
    mapMutations: mapOrderMutations,
    mapActions: mapOrderActions
} = createNamespacedHelpers('order')

export default {
    name: 'OrderForm',
    components: {
        MainLayout,
        PriceCalculator,
        AssignmentDetails,
        WriterSettings,
        QuoteSidebar
    },
    data: () => ({
        currentStep: 1,
        settingsBreadcrumbs: {
            title: 'New Order',
            steps: [
                {
                    title: 'Home',
                    url: '/'
                },
                {
                    title: 'New Order',
                    url: '/order/new'
                }
            ],
            additionalClass: 'bc-horizontal bc-checkout bc-order-form',
            excerpt: 'Estimated time is 5 minutes.'
        },
        mobileTotal: false,
        orderTotal: null,
        timer: null
    }),
    // created() {
    //     this.$root.$on('validateStep', (data) => this.validateStep(data))
    // },
    async beforeRouteEnter(to, from, next) {
        // this WORKAROUND is required because:
        //  1) we need to load the data from the API and store it in vuex
        //  2) component must not be rendered before we have the data in the store
        //  3) 'this' is not available, so we must import store
        if (store.state.config.pricing.length === 0) {
            await store.dispatch('config/GET_PRICING')
            next()
        } else {
            next()
        }
    },
    methods: {
        ...mapOrderMutations([
            CLEAR_FORM
        ]),
        ...mapOrderActions([
            ACTION_NEW_ORDER,
            ACTION_EDIT_ORDER
        ]),
        // validateStep(data) {
        //     const { step, valid } = data
        //     this.validationState[step] = valid
        // },
        onEventPrevious(value) {
            let headHight
            window.screen.width > 992 ? headHight = 80 : headHight = 60
            if (value === 3) {
                this.scrollTo('.assignment-details', -headHight)
            } else if (value === 2) {
                this.scrollTo('#move_price', -headHight)
            }
            this.currentStep -= 1
        },
        onEventNext(value) {
            let headHight
            window.screen.width > 992 ? headHight = 80 : headHight = 60
            console.log(headHight)
            if (value === 1) {
                this.currentStep = 2
                this.scrollTo('#move_asigment', -80)
            }
            if (value === 2) {
                this.currentStep = 3
                this.scrollTo('#move_writer', -10)
            }
        },
        scrollTo(to, headerOffset) {
            const scrollToStep = scroller()

            switch (this.currentStep) {
            case 2:
                scrollToStep(to, 500, { offset: headerOffset })
                break;
            case 3:
                scrollToStep(to, 500, { offset: headerOffset })
                break;
            default:
                scrollToStep(to, 500, { offset: headerOffset })
                break;
            }
        },
        async onEventReportProblem() {
            console.log('opening report problem dialog')
        },
        async onEventClearForm() {
            const modalTitle = 'Reset Form'
            const modalText = 'You are about to reset the order form and start over. Are you sure?'
            if (await this.$root.$confirm(modalTitle, modalText, { color: 'red' })) {
                this.currentStep = 1
                this[CLEAR_FORM]()
                this.scrollTo('#move_price', -60)
                eventBus.$emit('reset-form');
                eventBus.$emit('showSnackBar', 'Order form has been reset', 'info');
                this.$nextTick(() => this.$forceUpdate());
            }
        },
        async onEventSubmitOrder(data) {
            if (Number.isNaN(data.total)) {
                throw new Error('No total received');
            }
            // let vm = this
            // try {
            //     // this.submitLoading = true
            //     // // first we must delete order with our ID index and then push new order to array, not the other way around

            //     // if(this.mode == `edit`) {
            //     //     this.deleteOrder({
            //     //         index: this.$route.params.id
            //     //     })
            //     // }
            //     const unwrappedTotal = JSON.parse(JSON.stringify(data.total))
            //     const response = await this[ACTION_SAVE_ORDER]({
            //         router: this.$router,
            //         total: unwrappedTotal
            //     })

            //     const title = vm.$route.name == 'order_edit' ? 'The order has been edited' : 'The order has been added to cart!'
            //     // show success message with a snackbar
            //     eventBus.$emit('showSnackBar', title, 'success');
            //     // // notify google
            //     // vm.recordEventAddToCart()
            // } catch (e) {
            //     console.log(e)
            //     eventBus.$emit('showSnackBar', 'Something went wrong', 'error');
            // } finally {
            //     // vm.submitLoading = false
            // }
            try {
                let snackbarMessage = null
                if (data.mode === 'edit') {
                    this[ACTION_EDIT_ORDER](JSON.parse(JSON.stringify(data)))
                    snackbarMessage = 'Changes have been saved'
                } else {
                    this[ACTION_NEW_ORDER](JSON.parse(JSON.stringify(data)))
                    snackbarMessage = 'Order has been added to cart'
                }
                eventBus.$emit('showSnackBar', snackbarMessage, 'success')
                this.$router.push({ name: 'checkout' })
            } catch (error) {
                console.log(error)
            }
        }
    }
}
</script>

<style lang="scss">
@import '@/assets/styles/site_specific_styles/customessaymeister.com/index';
</style>
