<template>
    <section
        id="move_price"
        class="order-form-step price-calculator"
    >
        <header class="order-form-step__heading">
            <h1 class="order-form-step__heading">
                <span class="order-form-step__step-number">Step 1.</span> Price
                calculator
            </h1>
            <p
                v-if="currentStep === 1"
                class="order-form-step__description"
            >
                It is recommended that you log in to your account to be able to
                order extra pages or apply a discount or your store credit
                balance.//{{ linked_to_order }}
            </p>
            <CheckCircle
                v-if="isFormValid"
                class="order-form-step__confirm-icon"
            />
        </header>
        <section
            v-if="currentStep === 1"
            id="move_asigment"
            class="price-calculator__form"
        >
            <div v-if="isAuth && referenceOrderList">
                <div class="order-form-step__column row attach">
                    <form-switch
                        :id="'chk_linked'"
                        v-model="chk_linked"
                    />
                    <span class="order-form-step__switch-text">
                        I want to attach this order to an existing project
                    </span>
                </div>
                <div v-if="chk_linked">
                    <div class="order-form-step__column">
                        <form-select-project
                            v-model="$v.linked_to_order.$model"
                            label="Pretype order ID or choose from the list"
                            :items="referenceOrderList"
                            :styles="false"
                            :errors="validationMessage($v.linked_to_order)"
                            name="linked_to_order"
                            @change.native="$v.linked_to_order.$touch()"
                            @blur.native="$v.linked_to_order.$touch()"
                        />
                    </div>
                </div>
            </div>
            <div class="row space-between">
                <div
                    class="order-form-step__column order-form-step__column--half"
                >
                    <form-select
                        v-model="$v.type.$model"
                        label="Project type"
                        prepend-icon="FileDocumentIcon"
                        :items="calculatorData.type"
                        name="type"
                        :errors="validationMessage($v.type)"
                        @change.native="$v.type.$touch()"
                        @blur.native="$v.type.$touch()"
                    />
                    <span
                        class="custom-tooltip"
                    >
                        <p>?</p>
                        <span>Select the type of project that most closely describes your assignment (i.e. do not choose essay, if you have a complex research paper or report).</span>
                    </span>
                </div>

                <div
                    class="order-form-step__column order-form-step__column--half"
                >
                    <form-select
                        v-model="$v.academiclevel.$model"
                        prepend-icon="BookOpenVariantIcon"
                        :items="calculatorData.level"
                        label="Academic level"
                        tabindex="0"
                        name="level"
                        :errors="validationMessage($v.academiclevel)"
                        @change.native="$v.academiclevel.$touch()"
                        @blur.native="$v.academiclevel.$touch()"
                    />
                    <span class="custom-tooltip">
                        <p>?</p>
                        <span>Our writers will try to match the requested level through appropriate word choice, grammar style, and sentence structure.</span>
                    </span>
                </div>
            </div>

            <div class="row row--three-item space-between">
                <div
                    class="order-form-step__column order-form-step__column--half price-calculator__item-order price-calculator__item-order--1"
                >
                    <form-input-type-number
                        v-model="$v.pages.$model"
                        label="Pages"
                        :value="pages"
                        :field.sync="pages"
                        :show_value="true"
                        tabindex="0"
                        :min_value="0"
                        :errors="validationMessage($v.pages)"
                        @reduce="pages--"
                        @increase="pages++"
                        @change.native="$v.pages.$touch()"
                    />
                    <span class="custom-tooltip">
                        <p>?</p>
                        <span>All content is formatted as follows: 12 point Times New Roman font, 1 inch margins on all sides, double spacing, approx. 280 - 300 words per page.</span>
                    </span>
                </div>

                <div
                    class="order-form-step__column order-form-step__column--half order-form-step__column--half price-calculator__item-order price-calculator__item-order--2"
                >
                    <form-date-picker
                        v-model="$v.duedate.$model"
                        label="Due date"
                        tabindex="0"
                        :min_hours="3"
                        :errors="validationMessage($v.duedate)"
                        :field.sync="duedate"
                        @change.native="$v.duedate.$touch()"
                        @blur.native="$v.duedate.$touch()"
                    />
                    <span class="custom-tooltip">
                        <p>?</p>
                        <span>
                            We are open 24/7 and can do your project in as little as 3 hours, provided it is small enough to be done within the timeframe. Please be realistic about the deadline. Our writers need a minimum of 30 minutes to type 1 page and the research takes time too. To avoid disappointment, we suggest that you think of the deadline date that you specify in this order form as the moment you will receive your first draft rather than the date/time your project is due. While we do our best to meet all initial instructions with the first draft, there are times when a rewrite is inevitable and the writers need time to address your concerns. So please choose the deadline wisely so that you have sufficient time to review the draft and have the writer revise, if needed.
                        </span>
                    </span>
                </div>

                <div
                    v-if="pages > 0"
                    class="order-form-step__column order-form-step__column--half order-form-step__column--half price-calculator__item-order price-calculator__item-order--3"
                >
                    <div class="order-form-step__word-count">
                        {{ pages }} double spaced pages =  &#126; {{ pages * wordcount | number }} words
                    </div>
                </div>

                <div
                    v-if="duedate"
                    class="order-form-step__column order-form-step__column--half order-form-step__column--half price-calculator__item-order price-calculator__item-order--4"
                >
                    <div class="order-form-step__word-count">
                        {{ duedate | moment_from }} days to deadline
                    </div>
                </div>
            </div>

            <div class="switch-box">
                <div class="order-form-step__switch-row">
                    <form-switch
                        :id="'chk_slides'"
                        v-model="chk_slides"
                    />
                    <span class="order-form-step__switch-text">
                        Presentation slides (PowerPoint, etc.)
                    </span>
                </div>
                <div
                    v-if="chk_slides"
                    class="order-form-step__column order-form-step__column--half"
                >
                    <form-input-type-number
                        v-model="$v.slides.$model"
                        label="Slides"
                        :value="slides"
                        :field.sync="slides"
                        :show_value="true"
                        :min_value="1"
                        :init_value="1"
                        tabindex="0"
                        :errors="validationMessage($v.slides)"
                        @reduce="slides--"
                        @increase="slides++"
                        @change="$v.slides.$touch()"
                        @blur="$v.slides.$touch()"
                    />
                </div>
            </div>

            <div class="switch-box">
                <div class="order-form-step__switch-row">
                    <form-switch
                        :id="'chk_charts'"
                        v-model="chk_charts"
                        messages
                    />
                    <span class="order-form-step__switch-text">
                        Charts (tables, graphs, etc.)
                    </span>
                </div>
                <div
                    v-if="chk_charts"
                    class="order-form-step__column order-form-step__column--half"
                >
                    <form-input-type-number
                        v-model="$v.charts.$model"
                        label="Charts"
                        :value="charts"
                        :field.sync="charts"
                        :show_value="true"
                        :min_value="1"
                        :init_value="1"
                        tabindex="0"
                        :errors="validationMessage($v.charts)"
                        @reduce="charts--"
                        @increase="charts++"
                        @change="$v.charts.$touch()"
                        @blur="$v.charts.$touch()"
                    />
                </div>
            </div>

            <div class="switch-box">
                <div class="order-form-step__switch-row">
                    <form-switch
                        :id="'chk_problems'"
                        v-model="chk_problems"
                    />
                    <span class="order-form-step__switch-text">
                        Quantitative problems (accounting, finance,
                        statistics, algebra, calculus etc.)
                    </span>
                </div>
                <div
                    v-if="chk_problems"
                    class="order-form-step__column order-form-step__column--half"
                >
                    <form-input-type-number
                        v-model="$v.problems.$model"
                        label="Problems"
                        :value="problems"
                        :field.sync="problems"
                        :show_value="true"
                        :min_value="1"
                        :init_value="1"
                        tabindex="0"
                        :errors="validationMessage($v.problems)"
                        @reduce="problems--"
                        @increase="problems++"
                        @change="$v.problems.$touch()"
                        @blur="$v.problems.$touch()"
                    />
                </div>
            </div>
            <div v-if="isAuth">
                <div class="order-form-step__switch-row">
                    <form-switch
                        :id="'chk_discount'"
                        v-model="chk_discount"
                    />
                    <span class="order-form-step__switch-text">
                        I have a special discount code
                    </span>
                </div>
                <div
                    v-if="chk_discount"
                    class="order-form-step__column order-form-step__column--half order-form-step__column-discount"
                >
                    <form-input
                        v-model="$v.discount_code.$model"
                        legend="Discount code"
                        type="text"
                        name="title"
                        :field.sync="discount_code"
                        :errors="validationMessage($v.discount_code)"
                        @input.native="$v.discount_code.$touch();"
                        @blur.native="$v.discount_code.$touch();"
                    />
                </div>
            </div>

            <div
                v-if="!isAuth"
                class="quote-sidebar__login"
            >
                Please
                <router-link :to="{ name: 'login' }">
                    log in
                </router-link>
                to use a discount code
            </div>

            <div class="row row-btn">
                <button
                    :class="isFormValid ? 'order-form-step__button next' : 'order-form-step__button next disable'"
                    @click="prevalidate"
                >
                    next
                </button>
            </div>
        </section>
    </section>
</template>
<script>

// validation
import { validationMixin } from 'vuelidate'
import formValidationMixin from '@/mixins/formValidationMixin.js'
import { validationMessage } from 'vuelidate-messages'
import { validationRules } from '@/validation/order/RulesPriceCalculator.js'
import { formMessages } from '@/validation/order/MessagesPriceCalculator.js'

// Mixins
import filtersMixin from '@/mixins/filtersMixin.js'

// icons
import CheckCircle from 'mdi-vue/CheckCircle.vue'

// vuex
import { mapOrderFormFields } from '@/store/modules/order'
import { SET_VALIDATED_STEP } from '@/store/modules/order/mutation-types'
import { createNamespacedHelpers } from 'vuex'
import {
    GET_REFERENCE_ORDERS
} from '@/store/modules/config/action-types'
import { eventBus } from '@/helpers/event-bus';

const { mapActions: mapConfigActions } = createNamespacedHelpers('config')

const {
    mapMutations: mapOrderMutations
} = createNamespacedHelpers('order')

export default {
    name: 'PriceCalculator',
    components: {
        CheckCircle
    },
    mixins: [
        validationMixin,
        formValidationMixin,
        filtersMixin
    ],
    validations: {
        ...validationRules
    },
    props: {
        currentStep: {
            required: true,
            type: Number
        }
    },
    data() {
        return {
            wordcount: 280,
            chk_slides: false,
            chk_charts: false,
            chk_problems: false,
            chk_discount: false,
            chk_linked: false,
            referenceOrderList: null
        }
    },
    computed: {
        ...mapOrderFormFields([
            'form_data.type',
            'form_data.academiclevel',
            'form_data.pages',
            'form_data.duedate',
            'form_data.slides',
            'form_data.charts',
            'form_data.problems',
            'form_data.discount_code',
            'form_data.linked_to_order'
        ]),
        calculatorData() {
            return this.$store.state.config.pricing;
        },
        isButtonDisabled() {
            return false;
            // (!!this.pagesErrors && !!this.typeErrors && !!this.academiclevelErrors);
        },
        isAuth() {
            return this.$store.state.client.auth;
        },
        isFormValid() {
            return !this.$v.$invalid
        }
    },
    watch: {
        chk_slides(val) {
            if (val === false) {
                this.slides = 0;
            }
        },
        chk_charts(val) {
            if (val === false) {
                this.charts = 0;
            }
        },
        chk_problems(val) {
            if (val === false) {
                this.problems = 0;
            }
        },
        chk_discount(val) {
            if (val === false) {
                this.discount_code = '';
            }
        },
        chk_linked(val) {
            if (val === false) {
                this.linked_to_order = '';
            }
        },
        isFormValid(value) {
            this[SET_VALIDATED_STEP]({
                step: 0,
                name: 'Price Calculator',
                value
            })
        }
    },
    async mounted() {
        // this.linked_to_order = this.$route.query.linked_to_order;
        await this.setCheckboxValue();
        if (this.isAuth) {
            this.referenceOrderList = await this[GET_REFERENCE_ORDERS]();
            (this.linked_to_order)
                ? (this.chk_linked = true)
                : (this.chk_linked = false);
        }
        eventBus.$on('reset-form', () => {
            this.$v.$reset()
            this.setCheckboxValue()
            this.chk_linked = false
        });
    },
    methods: {
        ...mapOrderMutations([
            SET_VALIDATED_STEP
        ]),
        ...mapConfigActions([
            GET_REFERENCE_ORDERS
        ]),
        validationMessage: validationMessage(formMessages),
        setCheckboxValue() {
            this.slides !== 0
                ? (this.chk_slides = true)
                : (this.chk_slides = false);
            this.charts !== 0
                ? (this.chk_charts = true)
                : (this.chk_charts = false);
            this.problems !== 0
                ? (this.chk_problems = true)
                : (this.chk_problems = false);
            this.discount_code !== ''
                ? (this.chk_discount = true)
                : (this.chk_discount = false);
        },
        prevalidate() {
            this.validate()
        },
        submit() {
            this.$emit('eventNext', this.currentStep)
        },
        redirectToLogin() {
            this.$router.push({ name: 'login' });
        },
        editValue(type, value) {
            this.charts = value
            // type = value
        }
    }
}
</script>
