<template lang="html">
    <section class="order-form-step preference">
        <header class="order-form-step__heading">
            <h1 class="order-form-step__heading">
                <span class="order-form-step__step-number">Step 3.</span>Writer
                Preference
            </h1>
            <CheckCircle
                v-if="isFormValid"
                class="order-form-step__confirm-icon"
            />
        </header>

        <section
            v-if="currentStep === 3"
            class="writer-preference"
        >
            <div class=" order-form-step__column row">
                <form-switch
                    :id="'chk_writers'"
                    v-model="chk_writers"
                />
                <span>I want to choose a preferred writer or block certain writers from seeing my project</span>
            </div>

            <div
                v-if="chk_writers"
                class="writer-preference__input"
            >
                <div class=" order-form-step__column">
                    <form-select-writers
                        v-model="$v.preferred_writers.$model"
                        label="Preferred writers"
                        :writers="listWriters"
                        :styles="true"
                        :disabled_items="blocked_writers"
                        :errors="validationMessage($v.preferred_writers)"
                        name="preferred-writers"
                        @change.native="$v.preferred_writers.$touch()"
                        @blur.native="$v.preferred_writers.$touch()"
                    />
                </div>
                <div class=" order-form-step__column">
                    <form-select-writers
                        v-model="$v.blocked_writers.$model"
                        label="Blocked writers"
                        :writers="listWriters"
                        :styles="false"
                        :disabled_items="preferred_writers"
                        :errors="validationMessage($v.blocked_writers)"
                        name="blocked-writers"
                        @change.native="$v.blocked_writers.$touch()"
                        @blur.native="$v.blocked_writers.$touch()"
                    />
                </div>
            </div>

            <div class="row order-form-step__buttons">
                <button
                    class="order-form-step__button prev"
                    @click="goToPrevious"
                >
                    previous
                </button>
                <button
                    class="order-form-step__button order-form-step__button-done next"
                    :class="{'disable' : !isFormValid}"
                    @click="scrollTo"
                >
                    done
                </button>
            </div>
        </section>
    </section>
</template>

<script>
import { scroller } from 'vue-scrollto/src/scrollTo'

// icons
import CheckCircle from 'mdi-vue/CheckCircle.vue';

// validation
import { validationMixin } from 'vuelidate'
import formValidationMixin from '@/mixins/formValidationMixin'
import { validationMessage } from 'vuelidate-messages'
import { validationRules } from '@/validation/order/RulesWriterSettings'
import { formMessages } from '@/validation/order/MessagesWriterSettings'

// vuex
import { mapOrderFormFields } from '@/store/modules/order'
import { createNamespacedHelpers } from 'vuex'
import {
    GET_LIST_WRITERS
} from '@/store/modules/config/action-types'
import {
    SET_VALIDATED_STEP
} from '@/store/modules/order/mutation-types'

const { mapActions: mapConfigActions } = createNamespacedHelpers('config')
const { mapMutations: mapOrderMutations } = createNamespacedHelpers('order')

export default {
    name: 'WritersSettings',
    components: {
        CheckCircle
    },
    mixins: [
        validationMixin,
        formValidationMixin
    ],
    validations: {
        ...validationRules
    },
    props: {
        currentStep: {
            required: true,
            type: Number
        }
    },
    data() {
        return {
            // default setting of checkbox must be false
            chk_writers: false,
            listWriters: null
        }
    },
    computed: {
        ...mapOrderFormFields([
            'form_data.preferred_writers',
            'form_data.blocked_writers'
        ]),
        isFormValid() {
            return !this.$v.$invalid
        }
    },
    watch: {
        isFormValid(value) {
            this[SET_VALIDATED_STEP]({
                step: 2,
                name: 'Writer Settings',
                value
            })
        },
        chk_writers(value) {
            if (value === false) {
                this.preferred_writers = [];
                this.blocked_writers = [];
            }
        },
        currentStep() {
            // this is a workaround, we need this because when we get to this step, no validation is done
            // and the user is confused because the main validation button might be in the false status, even though it is TRUE
            this[SET_VALIDATED_STEP]({
                step: 2,
                name: 'Writer Settings',
                value: this.isFormValid
            })
        }
    },
    created() {
        this.initCheckboxStatus()
    },
    async mounted() {
        this.listWriters = await this[GET_LIST_WRITERS]();
    },
    activated() {
        this.$v.$touch()
    },
    methods: {
        ...mapOrderMutations([
            SET_VALIDATED_STEP
        ]),
        ...mapConfigActions([
            GET_LIST_WRITERS
        ]),
        validationMessage: validationMessage(formMessages),
        goToPrevious() {
            this.$emit('eventPrevious', this.currentStep)
        },
        initCheckboxStatus() {
            if (this.chk_writers === false) {
                if (this.preferred_writers.length > 0 || this.blocked_writers.length > 0) {
                    this.chk_writers = true
                }
            }
        },
        scrollTo() {
            const scrollToStep = scroller()
            scrollToStep('.sticky', 500, { offset: -60 })
        }
    }
}
</script>
