<template lang="html">
    <section class="order-form-step assignment-details">
        <header class="order-form-step__heading">
            <h1 class="order-form-step__heading">
                <span class="order-form-step__step-number">Step 2.</span> Assignment Details
            </h1>
            <CheckCircle
                v-if="isFormValid"
                class="order-form-step__confirm-icon"
            />
        </header>

        <section
            v-if="currentStep === 2"
            id="move_writer"
        >
            <div class=" order-form-step__column row row-subject">
                <form-select
                    v-model="$v.subject.$model"
                    :items="subjects"
                    :label="'Subject'"
                    tabindex="0"
                    name="subject"
                    :errors="validationMessage($v.subject)"
                    @input.native="$v.subject.$touch();"
                    @blur.native="$v.subject.$touch();"
                />
            </div>

            <div class="order-form-step__column row">
                <form-switch
                    :id="'chk_title'"
                    v-model="chk_writer_select_topic"
                />
                <span class="order-form-step__switch-text">
                    Let the writer choose the title
                </span>
            </div>

            <div class="order-form-step__column row">
                <form-input
                    v-model="$v.title.$model"
                    :disabled="chk_writer_select_topic"
                    legend="Title"
                    type="text"
                    name="title"
                    :info="'You can either provide a title or have the writer come up with one'"
                    :field.sync="title"
                    :is-valid="$v.title.$dirty && !$v.title.$anyError"
                    :errors="validationMessage($v.title)"
                    @input.native="$v.title.$touch();"
                    @blur.native="$v.title.$touch();"
                />
            </div>

            <div class="order-form-step__column row-textarea">
                <form-textarea
                    v-model="$v.details.$model"
                    legend="Detailed instructions"
                    placeholder="Tell us everything about your project. If you have instructions in a file, you can attach it later once the order is processed."
                    type="text"
                    name="details"
                    :field.sync="details"
                    :errors="validationMessage($v.details)"
                    @input.native="$v.details.$touch();"
                    @blur.native="$v.details.$touch();"
                />
                <span class="custom-tooltip">
                    <p>?</p>
                    <span>
                        Please be specific and detailed. Include all the requirements that you think are important. While you will be able to request a free revision/rewrite after completion,
                        we revise based on the original instructions, meaning that we will not be able to expand the paper free of charge,
                        if you ask for new things that werent originally requested (in that case you can easily purchase extra pages)
                    </span>
                </span>
            </div>

            <section class="section-checkbox">
                <div class="row space-between">
                    <div class="order-form-step__column order-form-step__column--half row">
                        <form-checkbox
                            v-model="chk_abstract"
                            class="order-form-step__checkbox"
                            :check="'abstract'"
                        />
                        <span> I need an abstract</span>
                        <span class="custom-tooltip">
                            <p>?</p>
                            <span>
                                It is a 150- to 250-word paragraph that provides readers with a quick overview of your essay or report and its organization.
                                It should express your thesis (or central idea) and your key points; it should also suggest any implications or applications of the research you discuss in the paper.
                            </span>
                        </span>
                    </div>

                    <div class="order-form-step__column order-form-step__column--half row">
                        <form-checkbox
                            v-model="chk_faxes"
                            class="order-form-step__checkbox"
                            :check="'faxes'"
                        />
                        <span> I  will fax sources</span>
                        <span class="custom-tooltip">
                            <p>?</p>
                            <span>
                                An outline allows a writer to categorize the main points, to organize the paragraphs into an order that makes sense, and to make sure that each paragraph/idea can be fully developed.
                                Essentially, an outline helps prevent a writer from getting stuck when performing the actual writing of the essay.
                            </span>
                        </span>
                    </div>
                </div>

                <div class="row space-between">
                    <div class="order-form-step__column order-form-step__column--half row">
                        <form-checkbox
                            v-model="chk_outline"
                            class="order-form-step__checkbox"
                            :check="'outline'"
                        />
                        <span> I need an outline</span>
                        <span class="custom-tooltip">
                            <p>?</p>
                            <span>If you have more than one project, checkout faster by entering all your projects in your cart and then paying only once.</span>
                        </span>
                    </div>

                    <div class="order-form-step__column order-form-step__column--half row">
                        <form-checkbox
                            v-model="chk_uploads"
                            class="order-form-step__checkbox"
                            :check="'uploads'"
                        />
                        <span> I will upload additional files</span>
                    </div>
                </div>

                <div class="order-form-step__column row specify">
                    <form-switch
                        :id="'chk_sources'"
                        v-model="chk_sources"
                    />
                    <span class="order-form-step__switch-text">
                        I need to specify the number of sources and citation style (otherwise, the writer will decide)
                    </span>
                </div>
            </section>

            <section>
                <div
                    v-if="chk_sources"
                    class="order-form-step-source"
                >
                    <div class="order-form-step__column row">
                        <form-switch
                            :id="'chk_sources_any'"
                            v-model="chk_sources_any"
                        />
                        <span class="order-form-step__switch-text">
                            Any amount of sources
                        </span>
                    </div>
                    <div class="row space-between">
                        <div class="order-form-step__column order-form-step__column--half">
                            <form-select
                                v-model="$v.style.$model"
                                :items="styles"
                                :label="'Citation style'"
                                tabindex="0"
                                name="style"
                                :errors="validationMessage($v.style)"
                                @input.native="$v.style.$touch();"
                                @blur.native="$v.style.$touch();"
                            />
                            <span class="custom-tooltip">
                                <p>?</p>
                                <span>
                                    Please be specific and detailed. Include all the requirements that you think are important. While you will be able to request a free revision/rewrite after completion,
                                    we revise based on the original instructions, meaning that we will not be able to expand the paper free of charge,
                                    if you ask for new things that werent originally requested (in that case you can easily purchase extra pages)
                                </span>
                            </span>
                        </div>

                        <div
                            v-if="!chk_sources_any"
                            class="order-form-step__column order-form-step__column--half"
                        >
                            <form-input-type-number
                                v-model="$v.sources.$model"
                                label="Sources"
                                :value="sources"
                                :show_value="true"
                                tabindex="0"
                                :min_value="1"
                                :init_value="1"
                                :errors="validationMessage($v.sources)"
                                @reduce="sources--"
                                @increase="sources++"
                                @change.native="$v.sources.$touch()"
                            />
                            <span class="custom-tooltip">
                                <p>?</p>
                                <span>
                                    Usually we ask the client to specify the depth of research. That will be the number of external sources required for the writer to complete the assignement.
                                </span>
                            </span>
                        </div>
                    </div>
                </div>
            </section>

            <div class="row order-form-step__buttons">
                <button
                    class="order-form-step__button prev"
                    @click="goToPrevious"
                >
                    previous
                </button>

                <button
                    :class="isFormValid ? 'order-form-step__button next' : 'order-form-step__button next disable'"
                    @click="prevalidate"
                >
                    next
                </button>
            </div>
        </section>
    </section>
</template>

<script>
import { validationMixin } from 'vuelidate'
import formValidationMixin from '@/mixins/formValidationMixin'
import { validationMessage } from 'vuelidate-messages'
import { validationRules } from '@/validation/order/RulesAssignmentDetails'
import { formMessages } from '@/validation/order/MessagesAssignmentDetails'

import CheckCircle from 'mdi-vue/CheckCircle.vue'

import { createNamespacedHelpers } from 'vuex'
import { SET_VALIDATED_STEP } from '@/store/modules/order/mutation-types'

import { mapOrderFormFields } from '@/store/modules/order'
import { eventBus } from '@/helpers/event-bus';

const { mapMutations: mapOrderMutations } = createNamespacedHelpers('order')

export default {
    name: 'AssignmentDetails',
    components: {
        CheckCircle
    },
    mixins: [
        validationMixin,
        formValidationMixin
    ],
    validations: {
        ...validationRules
    },
    props: {
        currentStep: {
            required: true,
            type: Number
        }
    },
    data() {
        return {
            chk_writer_select_topic: false,
            chk_sources: false,
            chk_sources_any: false
        }
    },
    computed: {
        ...mapOrderFormFields([
            'form_data.subject',
            'form_data.title',
            'form_data.details',
            'form_data.chk_abstract',
            'form_data.chk_outline',
            'form_data.chk_faxes',
            'form_data.chk_uploads',
            'form_data.style',
            'form_data.sources'
        ]),
        subjects() {
            return this.$store.state.config.pricing.subject;
        },
        styles() {
            return this.$store.state.config.pricing.style;
        },
        isFormValid() {
            return !this.$v.$invalid
        }
    },
    watch: {
        chk_writer_select_topic(value) {
            if (value === true) {
                this.title = 'Writer\'s choice'
            } else {
                this.title = ''
            }
        },
        isFormValid(value) {
            this[SET_VALIDATED_STEP]({
                step: 1,
                name: 'Assignment Details',
                value
            })
        },
        chk_sources(val) {
            if (val === false) {
                this.sources = 0;
                this.style = ''
            }
        }
    },
    mounted() {
        this.chk_faxes = false
        this.chk_uploads = false
        this.chk_writer_select_topic = false
        console.log(this.title)
    },
    created() {
        this.setCheckboxValue();
        eventBus.$on('reset-form', () => {
            this.$v.$reset()
        });
    },
    methods: {
        ...mapOrderMutations([
            SET_VALIDATED_STEP
        ]),
        validationMessage: validationMessage(formMessages),
        prevalidate() {
            this.validate()
        },
        goToPrevious() {
            this.$emit('eventPrevious', this.currentStep)
        },
        submit() {
            this.$emit('eventNext', this.currentStep)
        },
        setCheckboxValue() {
            this.title.includes('Writer\'s choice')
                ? (this.chk_writer_select_topic = true)
                : (this.chk_writer_select_topic = false);
        }
    }
}
</script>
